import TripSort from '../view/sort-view';
import TripFilter from '../view/trip-filter-view';
import HeaderTripInfoBlock from '../view/header-info-trip';
import TripPointListView from '../view/event-list-view';
import NoPointView from '../view/no-point-view';
import { render, RenderPosition } from '../framework/render';
import PointPresenter from './point-presenter';
import { SortType } from '../const';
import { sortingByPrice, sortingByDay, sortingByTime } from '../utils';

/**
 * @class       UI- (, , ).
 */
export default class TripPresenter {
  #tripContainer;
  #tripMainContainer;
  #tripFiltersContainer;
  #points;
  #destinations;
  #offers;
  #tripSortComponent;
  #tripListComponent;
  #tripFilterComponent;
  #pointPresenters;
  #currentSortType;

  /**
   * @constructor
   * @param {HTMLElement} tripContainer     ( `.trip-events`)
   * @param {Object} pointsModel    ,   
   */
  constructor(tripContainer, pointsModel) {
    this.#tripContainer = tripContainer;
    this.#tripMainContainer = document.querySelector('.trip-main');
    this.#tripFiltersContainer = document.querySelector('.trip-controls__filters');
    this.#points = pointsModel.points;
    this.#destinations = pointsModel.destinations;
    this.#offers = pointsModel.offers;
    this.#tripListComponent = new TripPointListView(); //    ul
    this.#tripFilterComponent = new TripFilter(); //   
    this.#pointPresenters = new Map(); // ()     (  )
    this.#currentSortType = SortType.DAY; //  ,    (  DAY)
  }

  init() {
    this.#renderContent();
  }

  #renderContent() {
    this.#renderHeader(); // 1  -
    this.#renderEmptyPage(); // 2   (   === 0)
    if (this.#points.length > 0) {
      //  ,  , ul    
      this.#renderSort(); // 3   
      this.#sortPoints(this.#currentSortType); // 4     Points
      this.#renderPoints(); // 5    
    }
  }

  #renderHeader() {
    render(new HeaderTripInfoBlock(), this.#tripMainContainer, RenderPosition.AFTERBEGIN); // 1.1       ,     
    this.#renderFilter(); // 1.2
  }

  // 1.2
  #renderFilter() {
    render(this.#tripFilterComponent, this.#tripFiltersContainer);
  }

  // 2
  #renderEmptyPage() {
    //      
    if (this.#points.length === 0) {
      render(new NoPointView(), this.#tripContainer); // 2.1
      // 2.2         
      const filterInputs = document.querySelectorAll('.trip-filters__filter-input');
      filterInputs.forEach((input) => {
        input.disabled = true;
      });
    }
  }

  #renderSort() {
    // 3.1   (   - )
    this.#tripSortComponent = new TripSort({
      onSortTypeChange: this.#handleSortTypeChange
    });
    // 3.3    
    render(this.#tripSortComponent, this.#tripContainer);
  }

  // 3.2    
  #handleSortTypeChange = (sortType) => {
    if (this.#currentSortType === sortType) {
      return;
    }
    this.#currentSortType = sortType; // 3.2.1       
    this.#sortPoints(this.#currentSortType); // 3.2.2     
    this.#tripListComponent.element.innerHTML = ''; // 3.2.3   
    this.#renderPoints(); // 3.2.4    
  };

  // 4       (   Points)
  #sortPoints = (sortType) => {
    switch (sortType) {
      case 'day':
        this.#points.sort(sortingByDay);
        break;
      case 'price':
        this.#points.sort(sortingByPrice);
        break;
      case 'time':
        this.#points.sort(sortingByTime);
        break;
    }
  };

  // 5   
  #renderPoints() {
    render(this.#tripListComponent, this.#tripContainer); // 5.1  ul 
    for (let i = 0; i < this.#points.length; i++) {
      // 5.2             
      const pointPresenter = new PointPresenter({
        pointListContainer: this.#tripListComponent.element,
        point: this.#points[i],
        destinations: this.#destinations,
        offers: this.#offers,
        closeForms: this.#closeAllForms,
      });
      pointPresenter.init(); // 5.3     ,   Point
      this.#pointPresenters.set(this.#points[i].id, pointPresenter); // 5.4    Map
    }
  }

  #closeAllForms = () => {
    this.#pointPresenters.forEach((presenter) => presenter.closeForm());
  };
}

